import {Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {Injector} from '@angular/core';
import {AuthenticationsService} from '../../../app/shared/services/authentication.service';
import {UtilsService} from '../../../app/shared/services/utils.service';
import {DepartmentService} from '../../../app/shared/services/department.service';
import {CallsService} from '../../../app/shared/services/calls.service';
import {DashboardService} from '../../../app/shared/services/dashboard.service';
import {CompanyService} from '../../../app/shared/services/company.service';
import {UserService} from '../../../app/shared/services/user.service';
import {TranslateService} from '@ngx-translate/core';

export class BaseComponent {
    public router: Router;
    public fb: FormBuilder;
    public authService: AuthenticationsService;
    public utilService: UtilsService;
    public departmentService: DepartmentService;
    public callsService: CallsService;
    public dashboardService: DashboardService;
    public companyService: CompanyService;
    public userService: UserService;
    public translateService: TranslateService;

    protected constructor(protected injector: Injector) {
        this.router = this.injector.get(Router);
        this.fb = this.injector.get(FormBuilder);
        this.authService = this.injector.get(AuthenticationsService);
        this.utilService = this.injector.get(UtilsService);
        this.departmentService = this.injector.get(DepartmentService);
        this.callsService = this.injector.get(CallsService);
        this.dashboardService = this.injector.get(DashboardService);
        this.companyService = this.injector.get(CompanyService);
        this.userService = this.injector.get(UserService);
        this.translateService = this.injector.get(TranslateService);
    }
}
