import {Component} from '@angular/core';
import {AuthenticationsService} from './shared/services/authentication.service';
import {Router} from '@angular/router';
import {TranslateService} from '@ngx-translate/core';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html'
})
export class AppComponent {
    constructor(private authService: AuthenticationsService, private router: Router, private translateService: TranslateService) {
        this.initRoutes();
    }

    initRoutes() {
        this.initLanguage();
        if (!this.authService.currentUser) {
            this.router.navigateByUrl('auth/login').then();
        }
    }


    initLanguage(): void {
        const lang = 'jp';
        this.translateService.use(lang);
    }

}
