import {Injectable} from '@angular/core';
import {HttpService} from '../../../@calls/services/http.service';
import {api} from '../../../@calls/config/api';
import {AuthenticationsService} from './authentication.service';

@Injectable({
    providedIn: 'root'
})
export class UserService {


    constructor(private http: HttpService, private authService: AuthenticationsService) {
    }

    getUsers() {
        const url = api.getAllUsersForCompany + '?id=' + this.authService.currentUser.company_id;
        return this.http.get(url);
    }

    removeUser(id) {
        const url = api.removeUsersForCompany + '?id=' + id;
        return this.http.get(url);
    }

    updateUser(data) {
        const url = api.updateUser;
        return this.http.post(url, data);
    }
}
