import {Component} from '@angular/core';
import {TemplateService} from '../../../shared/services/template.service';
import {Router, Event, NavigationEnd} from '@angular/router';
import {MenuItems} from '../../../../@calls/models/menu';
import {sideMenu} from '../../../../@calls/static-data/sideMenu';
import {AuthenticationsService} from '../../../shared/services/authentication.service';

@Component({
    selector: 'app-side-nav',
    templateUrl: './side-nav.component.html'
})
export class SideNavComponent {

    isCollapse: boolean;
    menuItems = this.authService.sideMenu;

    constructor(private tplSvc: TemplateService, private router: Router, private authService: AuthenticationsService) {
        router.events.subscribe((event: Event) => {
            if (event instanceof NavigationEnd) {
                if (window.innerWidth < 992) {
                    this.tplSvc.toggleSideNavCollapse(false);
                }
            }
        })
    }

    ngOnInit() {
        this.tplSvc.isSideNavCollapseChanges.subscribe(isCollapse => this.isCollapse = isCollapse);
    }

    toggleSideNavCollapse() {
        this.isCollapse = !this.isCollapse;
        this.tplSvc.toggleSideNavCollapse(this.isCollapse);
    }

    goToPage(page: MenuItems): void {
        this.router.navigateByUrl(page.url).then(() => {
            this.menuItems.forEach((item) => {
                item.active = false;
            });
            page.active = true;
        });
    }
}
