import {Routes} from '@angular/router';

// Layouts
import {CommonLayoutComponent} from './pages/common/common-layout.component';
import {AuthenticationLayoutComponent} from './pages/common/authentication-layout.component';

export const AppRoutes: Routes = [
    {
        path: '',
        redirectTo: '/pages/dashboard',
        pathMatch: 'full',
    },
    {
        path: 'pages',
        component: CommonLayoutComponent,
        children: [
            {
                path: 'dashboard',
                loadChildren: () => import('./pages/dashboard/dashboard.module').then(m => m.DashboardModule)
            },
            {
                path: 'department',
                loadChildren: () => import('./pages/departments/departments.module').then(m => m.DepartmentsModule)
            },
            {
                path: 'calls',
                loadChildren: () => import('./pages/calls/calls.module').then(m => m.CallsModule)
            },{
                path: 'add-company',
                loadChildren: () => import('./pages/companies/add-company/add-company.module').then(m => m.AddCompanyModule)
            }, {
                path: 'companies',
                loadChildren: () => import('./pages/companies/companies.module').then(m => m.CompaniesModule)
            }, {
                path: 'users',
                loadChildren: () => import('./pages/users/users.module').then(m => m.UsersModule)
            }, {
                path: 'add-users',
                loadChildren: () => import('./pages/users/add-users/add-users.module').then(m => m.AddUsersModule)
            }, {
                path: 'settings',
                loadChildren: () => import('./pages/settings/settings.module').then(m => m.SettingsModule)
            },
            {
                path: 'element',
                loadChildren: () => import('./pages/ui-elements/ui-elements.modules').then(m => m.UiElementsModule)
            }
        ]
    },
    {
        path: '',
        component: AuthenticationLayoutComponent,
        children: [
            {
                path: 'auth',
                loadChildren: () => import('./auth/auth.module').then(m => m.AuthModule)
            }
        ]
    }
];

