import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {environment} from '../../../environments/environment.prod';
import {MenuItems} from '../../../@calls/models/menu';
import {sideMenu, sideMenuAdmin} from '../../../@calls/static-data/sideMenu';
import {Router} from '@angular/router';
import {HttpService} from '../../../@calls/services/http.service';
import {FormGroup} from '@angular/forms';
import {api} from '../../../@calls/config/api';

@Injectable({
    providedIn: 'root'
})
export class AuthenticationsService {

    public currentUser: any;
    public sideMenu: MenuItems[] = sideMenuAdmin;

    constructor(
        public http: HttpClient, public https: HttpService, public router: Router
    ) {
        if (localStorage.getItem('currentUserCallsAdmin')) {
            this.currentUser = JSON.parse(localStorage.getItem('currentUserCallsAdmin'));
            if (this.currentUser.type === 'admin') {
                this.sideMenu = sideMenu;
            }
        }
    }

    async start_session(token: string, user: any) {
        user['token'] = token;
        localStorage.setItem('currentUserCallsAdmin', JSON.stringify(user));
        localStorage.setItem('loginDateCallsAdmin', new Date() + '');
        this.currentUser = user;
        if (this.currentUser.type === 'admin') {
            this.sideMenu = sideMenu;
        } else {
            this.sideMenu = sideMenuAdmin;
        }
    }

    public destroy_session(): void {
        localStorage.removeItem('currentUserCallsAdmin');
        localStorage.removeItem('loginDateCallsAdmin');
        this.currentUser = null;
        this.router.navigateByUrl('auth/login').then();
    }

    login(data: any): any {
        const body = JSON.stringify(data);
        const headers = new HttpHeaders();

        const url = environment.globalServerURL + 'login';

        return this.http.post(url, data, {headers, observe: 'response'});
    }

    private getToken(): string {
        if (this.currentUser && this.currentUser.token) {
            return this.currentUser.token;
        }
        return '';
    }

    public isAuthenticated(): boolean {
        return !!this.getToken();
    }

    public register(data: any): any {
        const url = environment.globalServerURL + 'register';
        return this.http.post(url, data);
    }

    public updateCompany(data: any): any {
        const url = environment.globalServerURL + 'company/updateRegister';
        return this.https.post(url, data);
    }


    isLoggedIn(): boolean {
        if (!this.currentUser) {
            return false;
        }
        if (!localStorage.getItem('loginDateCallsAdmin')) {
            return false;
        }
    }

    resetPassword(form: FormGroup): any {
        const url = api.resetPassword;
        return this.http.post(url, form);
    }

    updatePassword(form: FormGroup): any {
        const url = api.changePassword;
        return this.https.post(url, form);
    }
}
