import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {SharedModule} from './shared/shared.module';
import {TemplateModule} from './pages/template/template.module';
import {TemplateService} from './shared/services/template.service';

// Layout Component
import {CommonLayoutComponent} from './pages/common/common-layout.component';
import {AuthenticationLayoutComponent} from './pages/common/authentication-layout.component';

// Routing Module
import {AppRoutes} from './app.routing';

// App Component
import {AppComponent} from './app.component';
import {HTTP_INTERCEPTORS, HttpClient} from '@angular/common/http';
import {AuthenticationsService} from './shared/services/authentication.service';
import {CampHttpInterceptor} from '../@calls/services/http.interceptor';
import {LoginComponent} from './auth/login/login.component';
import {DepartmentService} from './shared/services/department.service';
import {CallsService} from './shared/services/calls.service';
import {CallsComponent} from './pages/calls/calls.component';
import {DashboardService} from './shared/services/dashboard.service';
import {AddCompanyComponent} from './pages/companies/add-company/add-company.component';
import {CompaniesComponent} from './pages/companies/companies.component';
import {CompanyService} from './shared/services/company.service';
import { UsersComponent } from './pages/users/users.component';
import {UserService} from './shared/services/user.service';
import { SettingsComponent } from './pages/settings/settings.component';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';

// @ts-ignore
// @ts-ignore
// @ts-ignore
@NgModule({
    imports: [
        BrowserModule,
        RouterModule.forRoot(AppRoutes, {useHash: false}),
        SharedModule,
        TemplateModule,
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: (createTranslateLoader),
                deps: [HttpClient]
            }
        }),
    ],
    declarations: [
        AppComponent,
        CommonLayoutComponent,
        AuthenticationLayoutComponent,
        LoginComponent,
        SettingsComponent
    ],
    providers: [
        TemplateService,
        AuthenticationsService,
        DepartmentService,
        CallsService,
        DashboardService,
        CompanyService,
        UserService,
        {provide: HTTP_INTERCEPTORS, useClass: CampHttpInterceptor, multi: true},
    ],
    bootstrap: [AppComponent]
})


export class AppModule {
}

export function createTranslateLoader(http: HttpClient) {
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}
