import {Injectable} from '@angular/core';
import {HttpService} from '../../../@calls/services/http.service';
import {api} from '../../../@calls/config/api';

@Injectable({
    providedIn: 'root'
})
export class DashboardService {

    constructor(private http: HttpService) {
    }

    getDetails(id) {
        const url = api.dashboard + '?id=' + id;
        return this.http.get(url);
    }

    getAdminDetails() {
        const url = api.adminDashboard;
        return this.http.get(url);
    }
}
