import {Injectable} from '@angular/core';
import {custom} from 'devextreme/ui/dialog';
import {from as fromPromise, Observable} from 'rxjs';
import notify from 'devextreme/ui/notify';
import {TranslateService} from '@ngx-translate/core';


@Injectable({
    providedIn: 'root'
})
export class UtilsService {

    constructor(private translateService: TranslateService) {
    }

    confirmationDialog(title: string, message: string): Observable<{}> {
        const dialog = custom({
            title,
            message,
            buttons: [{
                text: this.translateService.instant('app.ok'),
                onClick: () => {
                    return true;
                }
            }, {
                text: this.translateService.instant('app.cancel'),
                onClick: () => {
                    return false;
                }
            }
            ],
            showTitle: true
        });
        return fromPromise(dialog.show());
    }


    showServerErrorToast(): void {
        this.showErrorToast(this.translateService.instant('app.serverError'));
    }

    showConnectionErrorToast(): void {
        this.showErrorToast(this.translateService.instant('app.error'));
    }

    showSavedSuccessfullyToast(): void {
        this.showSuccessToast(this.translateService.instant('app.saved'));
    }

    showAddedSuccessfullyToast(): void {
        this.showSuccessToast(this.translateService.instant('app.success'));
    }

    showUpdatedSuccessfullyToast(): void {
        this.showSuccessToast(this.translateService.instant('app.update'));
    }

    showMergedSuccessfullyToast(): void {
        this.showSuccessToast('mergedSuccessfully');
    }

    showDeletedSuccessfullyToast(): void {
        this.showSuccessToast(this.translateService.instant('app.delete'));
    }


    showSuccessToast(message: string): void {
        notify({
            message,
            type: 'success',
            displayTime: 600,
            width: 500
        });
    }

    showErrorToast(message: string, time = 600): void {
        notify({
            message,
            type: 'error',
            displayTime: time,
            width: 500
        });
    }

    showInfoToast(message: string, time = 600): void {
        notify({
            message,
            type: 'info',
            displayTime: time,
            width: 500
        });
    }

    nestedLookupValueAdapter(value: string): any[] {
        value = (value || '').trim();
        if (!value) {
            return [];
        }
        const terms = value.split(new RegExp(',(?=(((?!\\)).)*\\()|[^\\(\\)]*$)', 'g'));
        return terms.map((term, idx) => {
            if (idx % 3 !== 0) {
                return null;
            }
            if (term.indexOf('(') >= 0) {
                return ({
                    value: +term.substring(0, term.indexOf('(')),
                    items: this.nestedLookupValueAdapter(term.substring(term.indexOf('(') + 1, term.lastIndexOf(')')))
                });
            } else {
                return ({
                    value: +term,
                    items: []
                });
            }
        }).filter(item => item !== null);
    }

    formatDate(date) {
        let d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();

        if (month.length < 2) {
            month = '0' + month;
        }
        if (day.length < 2) {
            day = '0' + day;
        }
        return [year, month, day].join('-');
    }
}
