import {Injectable} from '@angular/core';
import {HttpService} from '../../../@calls/services/http.service';
import {api} from '../../../@calls/config/api';
import {AuthenticationsService} from './authentication.service';

@Injectable({
    providedIn: 'root'
})
export class DepartmentService {

    constructor(private http: HttpService, private authService: AuthenticationsService) {
    }

    addDepartment(form): any {
        const url = api.addDepartment;
        return this.http.post(url, form);
    }

    updateDepartment(form): any {
        const url = api.updateDepartment;
        return this.http.post(url, form);
    }

    getDepartments(): any {
        let url = '';
        if (this.authService.currentUser.type === 'super_admin') {
            url = api.getDepartmentsAdmin;
        } else {
            url = api.getDepartmentsCompany + '?id=' + this.authService.currentUser.company_id;
        }
       // const url = this.authService.currentUser.type === 'super_admin' ? api.getDepartmentsAdmin : api.getDepartmentsCompany;
        return this.http.get(url);
    }

    removeDepartment(id): any {
        const url = api.removeDepartment + '?id=' + id;
        return this.http.get(url);
    }
}
