import {Injectable} from '@angular/core';
import {HttpService} from '../../../@calls/services/http.service';
import {api} from '../../../@calls/config/api';
import {AuthenticationsService} from './authentication.service';

@Injectable({
    providedIn: 'root'
})
export class CallsService {

    constructor(private http: HttpService, private authService: AuthenticationsService) {
    }

    getCalls(page, itemCount, id = 0): any {
        ++page;
        let url = '';
        if (this.authService.currentUser.type === 'super_admin') {
            url = api.getCallsForCompany + '?id=' + id + '&page=' + page + '&itemsCount=' + itemCount;
        } else {
            url = api.getCallsForCompany + '?id=' + this.authService.currentUser.company_id + '&page=' + page + '&itemsCount=' + itemCount;
        }
        return this.http.get(url);
    }

    removeCall(id): any {
        const url = api.removeCall + '?id=' + id;
        return this.http.get(url);
    }

    getCompanies(): any {
        const url = api.getCompaniesCall;
        return this.http.get(url);
    }
}
